import './Review.css';
import Chart from 'react-apexcharts';

const Review = () => {
const data = {
    series: [
        {
        name:'Revenue',
        data: [10, 50, 30, 90, 40, 120, 100],
        },
    ],
    options: {
        chart: {
          type: "area",
          height: "auto",
        },
  
        fill: {
          colors: ["#fff"],
          type: "gradient",
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
          colors: ["#ff929f"],
        },
        tooltip: {
          x: {
            format: "dd/MM/yy HH:mm",
          },
        },
        grid: {
          show: false,
        },
        xaxis: {
          type: "datetime",
          categories: [
            "2022-09-19T00:00:00.000Z",
            "2022-09-19T01:30:00.000Z",
            "2022-09-19T02:30:00.000Z",
            "2022-09-19T03:30:00.000Z",
            "2022-09-19T04:30:00.000Z",
            "2022-09-19T05:30:00.000Z",
            "2022-09-19T06:30:00.000Z",
          ],
        },
        yaxis: {
          show: false
        },
        toolbar:{
          show: false
        }
      },
};


    return <div className="review">
  <Chart series={data.series} type='area' options={data.options}  ></Chart>
        </div> ;
}
 
export default Review;